import React from 'react'
import {useSelector} from 'react-redux'
export default function Cates(props) {
    const state = useSelector(state => state)
    console.log(state)
    return (
        <div>
          numb of cates :  {state.cart[0].quantity!==undefined?state.cart[0].quantity:''}
        </div>
    )
}
