import React from 'react'
import './cartIcon.css'
import {Link} from 'react-router-dom'
import  {connect} from 'react-redux';
 function CartIcon(props) {
    return (
        <div >
           <Link to ='/cart'><i className='fa fa-shopping-cart' id='d'></i>
    <span className='badge badge-danger'>{props.totalQuatninity}</span>
            </Link>
        </div>
    )
}

const mapStateToProps=(state)=>{        
    return {
        totalQuatninity:state.cart.reduce((total,item)=>parseInt(total)+item.quantity,0),
} 
}

export default connect(mapStateToProps)(CartIcon)