import React from 'react'   
import Cartpice from './cartpice'    
import { connect} from 'react-redux'
import { clear_cart } from '../actions/actions'
 class Cart extends React.Component {   
    render(){
    return (
        <div style={{margin:'50px'}}>
            <h1>cart</h1>
          <div className='container'>  
            <div className='row'>
                {this.props.cartItem.map((item,index)=> <div key={item.product.id} className='p-5 col-lg-6 col-sm-12  sm-col-12 m-col-12'>
                    <Cartpice item={item} index={index} />
                </div>)}
            </div>
            </div>
            <h3>total : {this.props.total} </h3>
               <button onClick={this.props.clear} className='btn btn-primary btn-block'>pay</button>
        </div>
    )
}
}
const mapStateToProps=(state)=>{
    console.log(state)
return{
    cartItem:state.cart,
    total:state.cart.reduce((total,item)=>total+item.quantity*item.product.price,0),
}   
}

const mapDispatchToProps=(dispatch)=>{
    return{ clear:()=>dispatch(clear_cart()) }
}


export default connect(mapStateToProps,mapDispatchToProps)(Cart)
