import {ADD_TO_CART} from './types';
import {DELETE_FROM_CART} from './types';
import {CLEAR_CART} from './types';

export function addToCart (prodinfo,quatity){
return {
    type:ADD_TO_CART,
    prodinfo,
    quatity
}
}

export function deleteItem(index){
    return {
        type:DELETE_FROM_CART,
        index
    }
    }

    export function clear_cart(){
        return {
            type:CLEAR_CART,
        }
        }
