

import { createStore } from 'redux'
 import cartReducers from './reducers'

 const data=(state)=>{  
    
     try{
  const state = localStorage.getItem('cart')
  console.log(state) 
     if(state !== null|| state.length==0){
        console.log(state) 
         return  JSON.parse(state)
     }
    }catch(e){
        console.log(e)
    }
    
    return {cart:[...state,{product:{},quantity:{}}]};   
 }

 const save=(state)=>{
     localStorage.setItem('cart',JSON.stringify(state))
 }


const store=createStore(cartReducers,data(),window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__());
store.subscribe(
    ()=>{save(store.getState())}
)

export default store;