import React from 'react';
import './App.css';
import Home from './pages/Home';
import {
  BrowserRouter as Router,
  Route,
  Link
} from "react-router-dom";
import Cates from './pages/Cates'
import Products from './pages/Products'
import Product from './pages/Product'
import CartIcon from './compnenet/CartIcon';
import Cart from './pages/CartItem'
import store from '../src/store/store'
import {Provider} from 'react-redux'

function App()
 {
  return (
    <Provider store={store}>
    <Router className='App'>
    <div className="contatiner">
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <p className="navbar-brand" >catststore</p>
      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
    
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav mr-auto">
          <li className="nav-item active">
            <Link className="nav-link" to="/">Home <span className="sr-only">(current)</span></Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/products">products</Link>
          </li>
          {/* <li className="nav-item">
            <Link className="nav-link" to="/cates">cates</Link>
          </li> */}
        </ul>
        <CartIcon />
      </div>
      </nav>
      <Route path='/products' component={Products} />
      <Route path='/product/:id' component={Product} />
      <Route path='/cart' component={Cart} />
      <Route path='/cates' component={Cates} />
      <Route path='/' component={Home} exact />
    </div>
    </Router>
    </Provider> 
  );
}

export default App;
