import { ADD_TO_CART } from "../../actions/types";
import { DELETE_FROM_CART } from "../../actions/types";
import { CLEAR_CART } from '../../actions/types'
import { deleteItem } from '../../actions/actions' 

export default function  cartReducers(state,action){
    switch(action.type){
    case ADD_TO_CART :{
        return {
            cart:[
                ...state.cart,
                {
                    product:action.prodinfo,
                    quantity:action.quatity
                }
            ]
        }
    }
    case DELETE_FROM_CART :{
        const index=action.index;
        const new_item={...state}
         new_item.cart.splice(index,1)
        return new_item
    }
    
    case CLEAR_CART :{
        const new_item={...state}
        new_item.cart=[...state,{
             product:'',
            quantity:''
        }]

        return new_item
    }

    default :return state
    }
}
