import React from 'react'
import {getById} from   '../api/products'

import {addToCart} from '../actions/actions'

import {connect} from 'react-redux'
 class Product extends React.Component {
    state={produt:{},
    loading:true,
    quantity:0
}
    componentDidMount(){
       const  id=this.props.match.params.id;
        getById(parseInt(id)).then(product=>{
            this.setState({produt:product})
            this.setState({loading:false})
        })
    }

    change=(e)=>{

     if (e.target.value>=0)this.setState({quantity:parseInt(e.target.value)})
    }

addToCart=(product)=>{
    this.props.addToCart(product,this.state.quantity)
}

    render(){
        const product=this.state.produt;
        const O=this.state;

        if (this.state.loading) return 'loading ......'
        else 
    return (
        <div>
            <div className='row'>
                <div className='col-6'>
                    <img width='99%' title={this.state.produt.name} src={product.image} alt='no ' />
                </div>
                <div className='col-6'>
                <h1>{product.name}</h1>
    <p> price: {product.price} $ </p>
    <p>{product.description}</p>
    <br/>
    <input className='form-control' type='number' onChange={this.change} value={O.quantity} style={{width:'40%'}} /><br/><br />
    <p>total : {product.price*O.quantity}</p>
    <br />
    <button className='btn btn-primary' onClick={()=>this.addToCart(product)}> add to cart </button>
                </div>
            </div>


        </div>
    )
}   
}


const mapDispatchToProps=(dispatch)=>{
    return{
         addToCart:(prodinfo,quatity)=>dispatch(addToCart(prodinfo,quatity)),
        } 
 }

 export default connect(null,mapDispatchToProps)(Product)